.chat {
  flex: 0.7 1;
  display: flex;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.chat__header {
  display: flex;
  padding: 10px 16px;
  align-items: center;
  border-bottom: 1px solid lightgrey;
  background-color: rgb(240,242,245);
}

svg > .profile__pic{
  height: 40px;
  width: 40px;
}

.chat__headerInfo {
  flex: 1 1;
  padding-left: 15px;
  height: 40px;
}

.chat__headerInfo > h4 {
  font-weight:400;
  font-size: 16px;
}

.chat__headerInfo > p {
  color: grey;
  font-size: 13px;
}

.chat__body {
  flex: 1 1;
  padding: 30px;
  background-image: url(../../static/media/background.f4b196f1.png);
  background-repeat: repeat;
  overflow-y: auto;
  border-left: 1px solid #e9edef;
}

.chat__message {
  position: relative;
  padding: 5px;
  background-color: white;
  border-radius: 10px ; 
  width: -webkit-fit-content; 
  width: -moz-fit-content; 
  width: fit-content;
  margin-bottom: 10px;
  margin-left: 5%;
  box-shadow: 0px 3px 5px 0px rgba(181,181,181,1);
  z-index: 1;
}
.left:before { 
  content:""; 
  position: absolute; 
  left: -10px; 
  bottom: 2px; 
  width: 0; 
  height: 0; 
  border-right: 20px solid white; 
  border-top: 10px solid transparent;
}

.right:before { 
  content:""; 
  position: absolute; 
  right: -10px; 
  bottom: 2px; 
  width: 0; 
  height: 0; 
  border-left: 20px solid #dcf8c6; 
  border-top: 10px solid transparent;
  z-index: -1;
}

.chat__text{
  word-wrap: break-word;
  max-width: 250px;
  min-width: 70px;
  font-size: 14px;

}

.chat__sender {
  margin-left: auto;
  margin-right: 5%;
  background-color: #dcf8c6;
  padding: 2px 5px;
}

.chat__name {
  font-size:x-small;
  font-weight: 900;
  color:darksalmon;
  
}

.chat__time {
  font-size: xx-small;
  font-weight: bold;
  color: gray;
  margin-left: calc(100% - 25px);
}

.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 17px 5px 10px;
  border-top: 1px solid lightgrey;
}

.chat__footer > form {
  flex: 1 1;
  display: flex;
  background-color: white;
  border-radius: 8px;
  margin: 5px 10px;
  padding: 9px 12px 11px;
}

.chat__footer > form > input {
  flex: 1 1;
  border-style: none;
  margin: 0;
  padding: 0;
}

/* To keep button functionality but not dsiplay */
.chat__footer > form > button {
  display: none;
}





