.sidebar {
  flex: 0.3;
  /* overflow: overlay; */
  background-color: white;
  position: relative;
}

.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 10px 16px;
  background-color: rgb(240,242,245);
}

.sidebar__headerRight {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sidebar__headerRight > Button + Button {
  margin-left: 10px;
}

.sidebar__search {
  display: flex;
  align-items: center;
  background-color: white;
  height: 40px;
  padding: 4px 10px;
  border: 1px solid rgb(240,242,245);;
}

.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: rgb(240,242,245);
  width: 100%;
  height: 35px;
  border-radius: 8px;
}

.sidebar__searchContainer > .MuiSvgIcon-root {
  color: grey;
  padding: 10px 22.5px 10px 17.5px;
  width: 20px;
  height: 20px;
}

/* HIDE input outline */

input, input:focus,input:active {
  display: flex;
  background-color: transparent;
  outline: none;
  border: none;
  padding: 0;
}

/* Icon color */
path {
  color:rgb(84,101,111);

}

/* Doesn't allow the chats to exceed the app size */
.sidebar__chats__wrap{
  overflow-y: auto;
}

.logout-button{
  position: absolute;
  bottom: 0;
  margin-bottom: 15px;
  left: calc(50% - 75px);
  justify-content: center;
  border-radius: 20px;
	border: 1px solid #25d366;
	background-color: #25d366;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	cursor: pointer;
}